  public static async GetIsTokenValid(): Promise<boolean> {
    try {
      const refreshToken: string | null = LocalStorageService.getRefreshToken();

      if (!refreshToken || _.isEmpty(refreshToken)) {
        return false;
      }

      const res = await Axios.post<{ token: string }, AxiosResponse>(`${backendUrl}/auth/getistokenvalid`, {
        token: refreshToken
      });

      if (res?.data?.isValid) {
        return true;
      }
      return false;
    } catch (error: any) {
      KMainFunctions.exceptionHandler(error);
      return false;
    }
  }
