import React from "react";
import { BrowserRouter, HashRouter, Switch } from "react-router-dom";
import { Redirect } from "../auth/redirect";
import { NoMatch404 } from "../components/common/no-match-404";
import { Dashboard, ForgotPassword, Login, ResetPassword } from "../components/default_screens/index";
import { PrivateRoute } from "./private-route";
import { Route } from "./route";
import { EditJobDetails_Screen } from "../screens/editjobdetails/editjobdetails";
import { Authentication_Verification_Code_Screen } from "../screens/authentication_verification_code/authentication_verification_code";
import { CreateNewCount_Screen } from "../screens/createnewcount/createnewcount";
import { CreateNewJob_Screen } from "../screens/createnewjob/createnewjob";
import { EmployeeDepartmentList_Screen } from "../screens/employeedepartmentlist/employeedepartmentlist";
import { NewMonth_Screen } from "../screens/newmonth/newmonth";
import { RecruitmentForRoles_Screen } from "../screens/recruitmentforroles/recruitmentforroles";
import { CourseVideoDetails_Screen } from "../screens/coursevideodetails/coursevideodetails";
import { EditProfile_Screen } from "../screens/editprofile/editprofile";
import { MasterMenuLeftOnly_Screen } from "../screens/mastermenuleftonly/mastermenuleftonly";
import { PerformancePage_Screen } from "../screens/performancepage/performancepage";
import { EditEmployeePerformance_Screen } from "../screens/editemployeeperformance/editemployeeperformance";
import { NewYear_Screen } from "../screens/newyear/newyear";
import { JobEdit_Screen } from "../screens/jobedit/jobedit";
import { EmployeeList_Screen } from "../screens/employeelist/employeelist";
import { YearList_Screen } from "../screens/yearlist/yearlist";
import { Profile_Screen } from "../screens/profile/profile";
import { CourseEdit_Screen } from "../screens/courseedit/courseedit";
import { Authentication_Forgot_Password_Screen } from "../screens/authentication_forgot_password/authentication_forgot_password";
import { Dashboard_Screen } from "../screens/dashboard/dashboard";
import { TestScreen_Screen } from "../screens/testscreen/testscreen";
import { Sign_In_Screen } from "../screens/sign_in/sign_in";
import { EditCount_Screen } from "../screens/editcount/editcount";
import { Recruitment_Screen } from "../screens/recruitment/recruitment";
import { MonthList_Screen } from "../screens/monthlist/monthlist";
import { TrainingPage_Screen } from "../screens/trainingpage/trainingpage";
import { EmployeeDetails_Screen } from "../screens/employeedetails/employeedetails";
import { NewEmployeeDepartment_Screen } from "../screens/newemployeedepartment/newemployeedepartment";
import { Sign_Up_Screen } from "../screens/sign_up/sign_up";
import { MasterMenu_Screen } from "../screens/mastermenu/mastermenu";
import { NewEmployeePerformance_Screen } from "../screens/newemployeeperformance/newemployeeperformance";
import { CreateNewCourse_Screen } from "../screens/createnewcourse/createnewcourse";
import { Authentication_Reset_Password_Screen } from "../screens/authentication_reset_password/authentication_reset_password";



import { IAppMode, IAuthType } from "../bootstrapper";
import { KuikaInfo } from "../components/default_screens/kuika-info";
import { MfeLogin } from "../components/mfe/mfe-login";
import { MsalLogin } from "../components/msal/msal-login";
import { MsalRedirect } from "../components/msal/msal-redirect";
import { WFMailInteraction } from "../screens/wf-mail-interaction";
import { KMainFunctions } from "../shared/utilty/main-functions";

const confJson = require("../shared/json/conf.json");

interface IRouterState {}

export type IRouterType = "HashRouter" | "BrowserRouter";

interface IRouterProps {
  authType: IAuthType;
  routerType?: IRouterType;
  external_access_token?: string;
  external_refresh_token?: string;
  external_user?: string;
  appMode?: IAppMode;
}

declare let window: any;
export class Router extends React.Component<IRouterProps, IRouterState> {
  timerId: any = null;

  componentDidMount = () => {
    window.onfocus = function () {
      if (window.kuika === undefined) {
        window.kuika = {};
      }
      window.kuika.isTabActive = true;
    };

    window.onblur = function () {
      if (window.kuika === undefined) {
        window.kuika = {};
      }
      window.kuika.isTabActive = false;
    };

    if (!window.styler_speed) {
      window.styler_speed = 20;
    } else if (window.styler_speed < 500) {
      window.styler_speed += 100;
    }

    if (window?.kuika?.activeKuikaComponent !== undefined) {
      return;
    }
    const runTimeout = () => {
      this.timerId = setTimeout(() => {
        const allFormItems: NodeListOf<HTMLElement> = document.querySelectorAll(".kFormItem");
        if (allFormItems) {
          allFormItems.forEach((formItem) => {
            if (formItem.getAttribute("data-display-affected") === "true") {
              return;
            }
            const antFormItemControlInputContent: HTMLElement = formItem.querySelector(
              ".ant-form-item-control .ant-form-item-control-input .ant-form-item-control-input-content"
            );
            const antFormItemControlInput: HTMLElement = formItem.querySelector(
              ".ant-form-item-control .ant-form-item-control-input"
            );
            const antFormItemControl: HTMLElement = formItem.querySelector(".ant-form-item-control");

            if (
              antFormItemControlInputContent &&
              antFormItemControlInputContent.children &&
              antFormItemControlInputContent.children.length > 0
            ) {
              const formChild: HTMLElement = antFormItemControlInputContent.children[0] as HTMLElement;
              let calculatedDisplay: string = formChild.style.display;
              if (
                calculatedDisplay &&
                antFormItemControl &&
                antFormItemControlInput &&
                antFormItemControlInputContent
              ) {
                if (calculatedDisplay === "inline-flex" || calculatedDisplay === "inline-block") {
                  calculatedDisplay = "";
                  formItem.style.width = formChild.style.width ?? "auto";
                  formItem.style.display = "inline-block";
                  formItem.setAttribute("data-display-affected", "true");
                }
              }
            }
          });
        }
        this.componentDidMount();
      }, window.styler_speed);
    };

    runTimeout();

    setTimeout(() => {
      clearTimeout(this.timerId);
    }, 10000);
  };

  render() {
    const KuikaRouter = this.KuikaRouter();
    return (
      <>
        <KuikaRouter>
          <Switch key="Switch">
            <PrivateRoute exact path="/" component={TestScreen_Screen} />
                        <PrivateRoute exact path="/editjobdetails" component={EditJobDetails_Screen} />
            <Route exact path="/authentication_verification_code" component={Authentication_Verification_Code_Screen} />
            <PrivateRoute exact path="/createnewcount" component={CreateNewCount_Screen} />
            <PrivateRoute exact path="/createnewjob" component={CreateNewJob_Screen} />
            <PrivateRoute exact path="/employeedepartmentlist" component={EmployeeDepartmentList_Screen} />
            <PrivateRoute exact path="/newmonth" component={NewMonth_Screen} />
            <PrivateRoute exact path="/recruitmentforroles" component={RecruitmentForRoles_Screen} />
            <PrivateRoute exact path="/coursevideodetails" component={CourseVideoDetails_Screen} />
            <PrivateRoute exact path="/editprofile" component={EditProfile_Screen} />
            <PrivateRoute exact path="/performancepage" component={PerformancePage_Screen} />
            <PrivateRoute exact path="/editemployeeperformance" component={EditEmployeePerformance_Screen} />
            <PrivateRoute exact path="/newyear" component={NewYear_Screen} />
            <PrivateRoute exact path="/jobedit" component={JobEdit_Screen} />
            <PrivateRoute exact path="/employeelist" component={EmployeeList_Screen} />
            <PrivateRoute exact path="/yearlist" component={YearList_Screen} />
            <PrivateRoute exact path="/profile" component={Profile_Screen} />
            <PrivateRoute exact path="/courseedit" component={CourseEdit_Screen} />
            <Route exact path="/authentication_forgot_password" component={Authentication_Forgot_Password_Screen} />
            <PrivateRoute exact path="/dashboard" component={Dashboard_Screen} />
            <PrivateRoute exact path="/testscreen" component={TestScreen_Screen} />
            <Route exact path="/sign_in" component={this.LoginComponent()} />
            <PrivateRoute exact path="/editcount" component={EditCount_Screen} />
            <Route exact path="/recruitment" component={Recruitment_Screen} />
            <PrivateRoute exact path="/monthlist" component={MonthList_Screen} />
            <PrivateRoute exact path="/trainingpage" component={TrainingPage_Screen} />
            <PrivateRoute exact path="/employeedetails" component={EmployeeDetails_Screen} />
            <PrivateRoute exact path="/newemployeedepartment" component={NewEmployeeDepartment_Screen} />
            <Route exact path="/sign_up" component={Sign_Up_Screen} />
            <PrivateRoute exact path="/newemployeeperformance" component={NewEmployeePerformance_Screen} />
            <PrivateRoute exact path="/createnewcourse" component={CreateNewCourse_Screen} />
            <Route exact path="/authentication_reset_password" component={Authentication_Reset_Password_Screen} />

            <Route path="/redirect" component={Redirect} />
            <Route path={"/login"} component={this.LoginComponent()} />
            
            
            <Route exact path="/forgotpassword" component={ForgotPassword} />
            <Route exact path="/resetpassword" component={ResetPassword} />
            <PrivateRoute exact path="/dashboard" component={Dashboard} />
            <Route exact path="/kuikainfo" component={KuikaInfo} />
            <Route exact path="/msalredirect" component={MsalRedirect} />
            <Route exact path="/wfmailinteraction" component={WFMailInteraction} />
            <Route path="*" component={NoMatch404} />
          </Switch>
        </KuikaRouter>
      </>
    );
  }

  KuikaRouter = () => {
    // Web component case
    let isWebComponentApp: any;
    isWebComponentApp = this.props.appMode === "WebComponent";
    if (
      confJson?.domainName !== undefined &&
      confJson?.applicationName !== undefined &&
      confJson?.applicationName !== "" &&
      window.kuika !== undefined &&
      window.kuika?.apps !== undefined &&
      window.kuika?.apps.length !== undefined &&
      window.kuika?.apps.length > 0
    ) {
      window.kuika?.apps.forEach((app: any) => {
        if (app.appName === confJson.applicationName && app.workspace === confJson.domainName) {
          isWebComponentApp = true;
        }
      });
    }
    if (window.kuika === undefined) {
      window.kuika = {};
    }
    window.kuika.isWebComponentApp = isWebComponentApp;

    if (isWebComponentApp === true || this.props.routerType === "HashRouter") {
      return (props: any) => {
        return <HashRouter key="HashRouter">{props.children}</HashRouter>;
      };
    }
    return (props: any) => {
      if (KMainFunctions.virtualDirectoryURL === "auto" || window?.location?.protocol === "file:") {
        return <BrowserRouter key="BrowserRouter">{props.children}</BrowserRouter>;
      }
      return (
        <BrowserRouter basename={KMainFunctions.virtualDirectoryURL} key="BrowserRouter">
          {props.children}
        </BrowserRouter>
      );
    };
  };

  LoginComponent = () => {
    if (this.props.authType === "MSAL") {
      return MsalLogin;
    }

    const { external_access_token } = this.props;
    const { external_refresh_token } = this.props;
    const { external_user } = this.props;
    if (
      external_access_token !== null &&
      external_access_token !== undefined &&
      external_access_token !== "" &&
      external_refresh_token !== null &&
      external_refresh_token !== undefined &&
      external_refresh_token !== ""
    ) {
      return (props: any) => {
        return (
          <MfeLogin
            external_user={external_user}
            external_access_token={external_access_token}
            external_refresh_token={external_refresh_token}
          ></MfeLogin>
        );
      };
    }

    if (this.props.authType === "Kuika") {
      return Sign_In_Screen;
    }
    return <></>;
  };
}
