const screenInputData = {
  EditJobDetails: [{"name":"EmployeeID","type":"Guid","cardinality":1,"isExpandedPanel":null}],
	CourseVideoDetails: [{"name":"CourseVideoURL","type":"String","cardinality":1,"isExpandedPanel":null}],
	EditEmployeePerformance: [{"name":"ProductivityID","type":"Guid","cardinality":1,"isExpandedPanel":null}],
	JobEdit: [{"name":"JobID","type":"Guid","cardinality":1,"isExpandedPanel":null}],
	CourseEdit: [{"name":"CourseID","type":"Guid","cardinality":1,"isExpandedPanel":null}],
	EditCount: [{"name":"CompanyCountID","type":"Guid","cardinality":1,"isExpandedPanel":null}],
	EmployeeDetails: [{"name":"EmployeeID","type":"Guid","cardinality":1,"isExpandedPanel":null}],
	Sign_Up: [{"name":"JobTitle","type":"String","cardinality":1,"isExpandedPanel":null},{"name":"JobDepartment","type":"Guid","cardinality":1,"isExpandedPanel":null},{"name":"JobSalary","type":"Double","cardinality":1,"isExpandedPanel":null}]
};

export class ScreenInputHelper {
  public static getScreenInputValue(screenName: string, inputName: string, value: string): any {
    switch (this.getScreenInputType(screenName, inputName)) {
      case "String":
        return value;
      case "Boolean":
        return value.toLowerCase() === "true";
      // TODO
    }

    return value;
  }

  private static getScreenInputType(screenName: string, inputName: string): string {
    const data = screenInputData[screenName]?.find((x) => x.Name === inputName);
    return data?.TypeName ? data.TypeName : "";
  }
}
