  public static getPhotoFromGallery = () =>
    new Promise((resolve, reject) => {
      let listener = false;
      const input = document.createElement("input");
      input.classList.add("kuika-take-photo-gallery");
      input.setAttribute("type", "file");
      input.setAttribute("name", "kuika_photo_take");
      input.style.display = "none";
      input.accept = "image/*,application/pdf";
      listener = true;

      window.addEventListener("focus", () => {
        setTimeout(() => {
          if (listener) {
            listener = false;
            let files = Array.from(input.files);
            if (files.length === 0) {
              resolve("");
            }
          }
        }, 1000);
      });

      input.onchange = () => {
        let files = Array.from(input.files);
        if (files.length === 0) {
          resolve("");
        }

        const [file] = files;

        const reader = new FileReader();
        reader.addEventListener("load", (e) => {
          resolve(e.target.result);
        });
        reader.addEventListener("error", (e) => {
          reject(e.target.error);
        });

        reader.readAsDataURL(file);
      };

      input.click();
    });

  public static getPhotosFromGallery = (): Promise<{
    data: { dataBase64: string; name: string; size: number; fileExtension: string }[];
  }> => {
    return new Promise((resolve) => {
      const input = document.createElement("input");
      input.type = "file";
      input.multiple = true;
      input.accept = "image/*";

      input.onchange = async () => {
        try {
          const files = Array.from(input.files);
          const filePromises = files.map(
            (file) =>
              new Promise<{ dataBase64: string; name: string; size: number; fileExtension: string }>((fileResolve) => {
                const reader = new FileReader();
                reader.onload = (e) => {
                  fileResolve({
                    dataBase64: e.target.result as string,
                    name: file.name,
                    size: file.size,
                    fileExtension: KMainFunctions.getExtensionByMimeType(file.type)
                  });
                };
                reader.onerror = () => {
                  fileResolve(null);
                };
                reader.readAsDataURL(file);
              })
          );

          const results = await Promise.all(filePromises);
          resolve({ data: results.filter((result) => result !== null) });
        } catch (error) {
          resolve({ data: [] });
        }
      };

      input.oncancel = () => {
        resolve({ data: [] });
      };

      input.click();
    });
  };

